import React, { useState, useEffect } from "react";
import { ListGroup, ListGroupItem, Card} from "react-bootstrap";
import { config } from "../configs/endpoints.js";
import { useLocation } from "react-router-dom";
import MapComponent from "../components/googleMap.js";
import { Link } from 'react-router-dom';
import axios from "axios";


function VisitDetail() {
  const [visit, setVisit] = useState(null);
  const [center, setCenter] = useState(null);
  const search = useLocation().search;
  const id = new URLSearchParams(search).get("id");
  useEffect(() => {
    axios.get(`http://stronglasstough.com/velan/api.php?getVisits`).then((response) => {
      const visit = response.data?.visits.filter((u) => u.id == id)[0];
      setVisit(visit);
      setCenter({
        lat: Number(visit?.lat),
        lng: Number(visit?.lng)
      });
      console.debug("Visits:", visit);
    });
  }, []);
  
  function getEndpoint() {
    return `${config.endpoints.basePath}${config.endpoints.login}`;
  }

  return (
    <div>
      <div className="d-flex align-items-center auth px-0">
        <div className="row w-100 mx-0">
          <div className="col-lg-12 mx-auto">
            <div className="page-header">
              <h3 className="page-title"> Visit Detail</h3>
              <Link className="btn btn-primary" to={{pathname: `/pages/visits`}}>Back</Link>
            </div>
            <div className="row mx-0">
            <div className="col-lg-6 mx-auto">
            <Card style={{ width: "auto" }}>
              <ListGroup className="list-group-flush">
                <ListGroupItem><h4 className="text-left py-3 font-weight-bold">Branch</h4>{visit?.branch}</ListGroupItem>
              </ListGroup>
              <h4 className="text-left py-3 px-3 font-weight-bold">Customer</h4>
              <ListGroup className="list-group-flush">
                <ListGroupItem>Name: {visit?.customer_name}</ListGroupItem>
                <ListGroupItem>Mobile: {visit?.customer_mobile_number}</ListGroupItem>
                <ListGroupItem>Type: {visit?.person_type}</ListGroupItem>
                <ListGroupItem>Address: {visit?.customer_address}</ListGroupItem>
              </ListGroup>
              <h4 className="text-left py-3 px-3 font-weight-bold">Buyer</h4>
              <ListGroup className="list-group-flush">
                <ListGroupItem>Name: {visit?.buying_person_name}</ListGroupItem>
                <ListGroupItem>Mobile: {visit?.buying_person_mobile_number}</ListGroupItem>
                <ListGroupItem>Type: {visit?.buying_person_mobile_number}</ListGroupItem>
                <ListGroupItem>Requirement: {visit?.requirement}</ListGroupItem>
              </ListGroup>
              <h4 className="text-left py-3 px-3 font-weight-bold">Building</h4>
              <ListGroup className="list-group-flush">
                <ListGroupItem>Building Area: {visit?.building_area}</ListGroupItem>
                <ListGroupItem>Building Type: {visit?.building_type}</ListGroupItem>
              </ListGroup>
              <h4 className="text-left py-3 px-3 font-weight-bold">Photo</h4>
              <Card.Img className="px-2 pb-2" variant="top" src={visit?.file_path} />
            </Card>
            </div>
            <div className="col-lg-6 mx-auto">
            <Card style={{ width: "auto" }}>
              <Card.Body>
                <Card.Title>Location</Card.Title>
                <MapComponent center = {center}
                >
                  { /* Child components, such as markers, info windows, etc. */ }
                  <></>
                </MapComponent>
              </Card.Body>
            </Card>
            </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default VisitDetail;
